export default {
  attached () {
    this.$parent.$trois.add(this.$trois)
    this.$dispatch('update')
  },
  detached () {
    this.$parent.$trois.remove(this.$trois)
    this.$dispatch('update')
  },
  events: {
    replace (oldTrois) {
      this.$parent.$trois.remove(oldTrois)
      this.$parent.$troid.add(this.$trois)
    }
  }
}



